import React from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";


import Navbar from "./components/navbar.component"
import ExercisesList from "./components/exercises-list.component";
import GearList from "./components/gear-list.component";
import EditExercise from "./components/edit-exercise.component";
import CreateExercise from "./components/create-exercise.component";
import CreateUser from "./components/create-user.component";
import CreateGear from './components/create-gear.component';
import EditGear from "./components/edit-gear.component";


function App() {
        return (
                <Router>
                        <div className="container">
                                <Navbar />
                                <br/>
                                <Route path="/" exact component={ExercisesList} />
                                <Route path="/edit/:id" component={EditExercise} />
                                <Route path="/gearlist" exact component={GearList} />
                                <Route path="/createuser" component={CreateExercise} />
                                <Route path="/users" component={CreateUser} />
                                <Route path="/creategear" component={CreateGear} />
                                <Route path="/editgear/:id" component={EditGear} />
                        </div>
                </Router>
);
}


export default App;